@import "mediaQuaries";
.login-box{
    width: 500px;
}
.login-card-body{
    background: rgba($color: #fff, $alpha: 0.1) !important;
    box-shadow: 10px 10px 10px;
}
.cus-card{
    background: rgba($color: #fff, $alpha: 0.1) !important;
}
.navbar-light .btn-navbar, .navbar-light .form-control-navbar
{
    background: transparent;
}
.content-wrapper {
    background: #fff;
}
.ltn__tab-menu-list .nav a.active {
    background-color: #171B2A;
    color: var(--white);
}
.ltn__tab-menu-list .nav a {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 15px 20px;
    border-bottom: 1px solid #e5eaee;
    color: #5c727d;
}

.ltn__tab-menu-list .nav {
    display: block;

    border: 1px solid #eee;
}


.tab-content {
    width: 100%;
}

.tab-content .tab-pane {
    display: block;
    height: 0;
    max-width: 100%;
    visibility: hidden;
    overflow: hidden;
    opacity: 0;
}

.tab-content .tab-pane.active {
    height: auto;
    visibility: visible;
    opacity: 1;
    overflow: visible;
}
.ltn__myaccount-tab-content-inner > p {
    padding: 20px;
    background-color: #f2f6f7;
}
p {
    color: #5c727d;
    margin-bottom: 1.5em;
    -webkit-hyphens: auto;
    -moz-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
}
a, a:hover, a:focus, a:active {
    text-decoration: none;
    outline: none;
    color: inherit;
}

.nav a:hover,a:hover {
    color: #ff5a3c ;
    text-decoration: none;

}

input[type=text], input[type=email], input[type=password], input[type=submit], input[type=date], textarea {
    background-color: var(--white);
    border: 2px solid;
    border-color:#e4ecf2;
    height: 50px;
    box-shadow: none;
    padding-left: 20px;
    font-size: 16px;
    color: #5c727d;
    width: 100%;
    margin-bottom: 30px;
    border-radius: 0;
    padding-right: 20px;
}


span.select2-selection.select2-selection--single {
    height: 50px;
    border: 1px solid #ced4da ;

}

.select2-container--default .select2-selection--single .select2-selection__rendered {
    color: #444;
    line-height: 40px !important;
}
.select2-container--default .select2-selection--single .select2-selection__arrow
{
    top: 10px !important;
}

.slider{
    width: 60px;
}

